import "./App.css";
import { useState } from "react";
import userEvent from "@testing-library/user-event";

function App() {
  let logo = "newmesuper";
  let [title, setTitle] = useState([
    "헝가리 여행 가이드",
    "마시멜로 쿠키 만들기",
    "맛집탐방 - 제니피자",
    "천재견 🐶 '잠돌이'",
    "Belle의 캘리포니아 한달살이",
  ]);

  const dateNow = new Date();
  const today = dateNow.toISOString().slice(0, 10);
  let [date, setDate] = useState(["", "", "", "", ""]);

  let [count, setCount] = useState([0, 0, 0, 0, 0]);
  let [modal, setModal] = useState(false);
  let [nums, setNums] = useState(0); //헤맨 부분
  let edit = [
    "some company is overdue",
    "초코송이 맛있어",
    "김유미씨의 로또 1등 당첨소감",
    "아디다스 나이키 룰루레몬",
    "밍키미용실",
  ];
  let [input, setInput] = useState("");

  let titleStorage = [...title];
  let dateStorage = [...date];
  let countStorage = [...count];

  return (
    <div className="App">
      <div className="black-nav">
        <h4 style={{ color: "yellow" }}>{logo}</h4>
      </div>
      {/* --------  글제목 넣기  ---------*/}

      {title.map((el, i) => {
        return (
          <div className="list" key={i}>
            <h4
              onClick={() => {
                setModal(!modal);
                setNums(i);
              }}
            >
              {el}
            </h4>
            <span
              onClick={() => {
                {
                  /* -----------------  좋아요 카운터 넣기  ---------------------*/
                  countStorage[i]++;
                  setCount(countStorage);
                }
              }}
            >
              ❤
            </span>
            <span>{count[i]}</span>
            <div>{date[i]}</div>

            <button
              className="deleteBtn"
              onClick={() => {
                titleStorage.splice(i, 1);
                setTitle(titleStorage);

                dateStorage.splice(i, 1);
                setDate(dateStorage);

                countStorage.splice(i, 1);
                setCount(countStorage);
              }}
            >
              글삭제
            </button>
          </div>
        );
      })}

      <button
        onClick={() => {
          titleStorage.sort(function (a, b) {
            if (a < b) {
              return -1;
            } else {
              return 1;
            }
          });
          setTitle(titleStorage);
        }}
      >
        가나다순
      </button>

      <input
        type="text"
        onChange={(e) => {
          input = e.target.value;
          setInput(input);
        }}
      ></input>
      <button
        onClick={() => {
          titleStorage.unshift(input);
          input == "" ? alert("내용을 입력하세요") : setTitle(titleStorage);

          dateStorage.unshift(today);
          setDate(dateStorage);
        }}
      >
        업로드
      </button>

      {modal == false ? null : (
        <ModalComp
          title={title}
          nums={nums}
          edit={edit}
          setTitle={setTitle}
        ></ModalComp>
      )}
    </div>
  );
}

{
  /* --------  모달 함수 넣기  ---------*/
}
function ModalComp(props) {
  return (
    <div className="modal">
      <h4
        onClick={() => {
          let titleStorage = [...props.title];
          let editStorage = [...props.edit];
          titleStorage[props.nums] = editStorage[props.nums];
          props.setTitle(titleStorage);
        }}
      >
        {props.title[props.nums]}
      </h4>
    </div>
  );
}

export default App;
